<template>
  <div id="home-page">
    <head-top title="我的首页" :show-back="false"></head-top>
    <div class="main">
      <div class="bgshouy"></div>
      <div class="syContent">
        <van-row>
          <van-col span="8">
            <span>
              <img src="../assets/icon_fx_normal.png" alt />
            </span>
          </van-col>
          <van-col span="8">
            <span class="bgblue">
              <img src="../assets/icon_yx_normal.png" alt />
            </span>
          </van-col>
          <van-col span="8">
            <span class="bgyellow">
              <img src="../assets/icon_zy_normal.png" alt />
            </span>
          </van-col>
        </van-row>
        <van-row>
          <van-col span="8">预习</van-col>
          <van-col span="8">复习</van-col>
          <van-col span="8">作业</van-col>
        </van-row>
      </div>
    </div>
  </div>
</template>

<script>
import headTop from "@/components/headTop";
import { Row, Col } from "vant";

export default {
  name: "",
  components: {
    [Row.name]: Row,
    [Col.name]: Col,
    headTop
  },
  props: {},
  data() {
    return {};
  },
  computed: {},
  watch: {},
  created() {},
  methods: {}
};
</script>

<style lang='less'>
#home-page {
  width: 100%;
  height: 100%;

  .bgshouy {
    background: url("../assets/bg_shouye.png");
    width: 100vw;
    height: 27vh;
    background-size: cover;
    margin-bottom: 5vh;
  }

  .syContent {
    width: 92vw;
    height: 15vh;
    margin: 0 auto;
    padding-top: 17px;
    background: rgba(255, 255, 255, 1);
    border-radius: 3px;
    font-size: 1.17rem;
    color: #333;

    .van-col--8 {
      height: 50px;
    }

    span {
      display: inline-block;
      width: 48px;
      height: 48px;
      background: rgba(0, 203, 171, 1);
      border-radius: 50%;

      img {
        width: 25px;
        height: 23px;
        padding: 12px 13px;
      }
    }

    .bgblue {
      background: #51bef1 !important;
    }

    .bgyellow {
      background: #f49f4d !important;
    }
  }
}
</style>



